* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  list-style: none;
}
body {
  background-color: green;
}
img {
  width: 100%;
}

p, ul {
  margin-top: 2rem;
}

.app {
  border-radius: 5px;
  border: 2px dotted purple;
  margin: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 500px;
  background-color: yellow;
}

.header {
  padding: 2rem 4rem;
  text-align: center;
  background-color: purple;
  color: yellow;
  font-size: 1.5rem;
}

.recipe {
  text-align: left;
  padding: 2rem 2rem;
}

.primaryBtn {
  margin: 1rem 0;
  padding: 2rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: orange;
  border: 4px solid fuchsia;
  color:purple;
  font-size: 1rem;
  border-radius: 5px;
}


